	.active .swiper_tab_line {
		border-bottom: 4rpx solid #FEDE33;
		width: 50rpx;
		margin: auto;
		margin-top: 17rpx;
		background-color: #0B9C13;
	}

	.uni_swiper_tab {
		border-bottom: 2rpx solid #eeeeee;
